import axios from 'axios';
class Axios {
	constructor() {
		this.defaultURL = 'https://imin.nonap.co/public/crm';
	}
	get(url) {
		return axios.get(`${this.defaultURL}/${url}`);
	}
	post(url, data, headers) {
		return axios.post(`${this.defaultURL}/${url}`, data, headers);
	}
}
export default Axios;
